import { useMemo } from 'react';
import { data } from '@/components/routes/routes';
import type { RoutesData } from '@/types/routes-types';

export function useRoutes() {
  const routes = useMemo<RoutesData>(() => data, []);

  const getMainNav = () => routes.navMain;
  const getCloudNav = () => routes.navClouds;
  const getSecondaryNav = () => routes.navSecondary;
  const getDocuments = () => routes.documents;
  const getUser = () => routes.user;

  return {
    routes,
    getMainNav,
    getCloudNav,
    getSecondaryNav,
    getDocuments,
    getUser,
  };
} 